@import 'colors';

.form {
	text-align: center;
	display: flex;
	flex-direction: column;
	fieldset {
		padding: 0;
		border: 0;
		margin: 0;
		margin-bottom: 1.5rem;
		display: flex; }
	input {
		flex: 1 1 0; }
	legend {
		margin-bottom: .75rem;
		font-size: large; }
	label {
		flex: 1 1 0;
		&:not(:last-child) {
			margin-right: .5rem; }
		input {
			width: 100%;
			padding: .3rem;
			border: 1px solid $light-color;
			border-radius: .3rem; } } }

.button {
	padding: .25rem 0rem;
	text-align: center;
	display: block;
	&:not(:last-child) {
		margin-right: .5rem; } }

.btnContainer {
	display: flex;
	justify-content: center;
	align-content: center; }
